.search-form {
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  text-align: center;
  margin: 40px;
}

.search-form input {
  padding: 10px;
  border-radius: 10px;
  outline: none;
  width: 300px;
  border-color: #333333;
  margin-right: 20px;
}

.search-form button,
.wrapper > button {
  padding: 12px 20px;
  border-radius: 10px;
  outline: none;
  border: none;
  cursor: pointer;
  color: #fff;
  background-color: #6c757d;
  border-color: #6c757d;
}

.search-form button:hover,
.wrapper > button:hover {
  background-color: #5c636a;
  border-color: #565e64;
}

.wrapper > button {
  display: block;
  margin: 0 auto;
}
